const uni = require('uni').default;
















import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'scanCode',
			result: ''
		}
	},
	onUnload:function(){
		this.result = '';
	},
	methods: {
		scanCode: function () {
			var that = this
			uni.scanCode({
				success: function (res) {
					that.result = res.result
				},
				fail: function (res) {}
			})
		}
	},
	components: {
		pageHead

	}
}



